import { ADD_TASK, ADD_TASKS, DELETE_TASK, DELETE_ALL_TASKS } from "./actions";

export const reducer = {
  tasks(state = [], { type, ...payload }) {
    switch (type) {
      case ADD_TASK:
        return [
          ...state,
          {
            id: payload.id,
            name: payload.name,
            startTime: payload.startTime,
            endTime: payload.endTime,
            spendTime: payload.endTime - payload.startTime
          }
        ];

      case ADD_TASKS:
        return payload.tasks.map((task) => {
          return { ...task };
        });

      case DELETE_TASK:
        return state.filter((task) => task.id !== payload.id);

      case DELETE_ALL_TASKS:
        return [];

      default:
        return state;
    }
  }
};
